@import "ScssHelpers/helpers-all.scss";

.category_slider {
	position: relative;
}

.category_slider__button {
	position: absolute;
	top: 50%;
	z-index: 3;

	&.category_slider__button--prev_mod {
		left: 0;
		transform: translate(50%, -50%) rotate(180deg);
		
		@include bp($point_2, min) {
			margin-left: calc(50% - 50vw - 4px);
		}
		@include bp($point_3) {
			margin-left: calc(50% - 50vw - 1.2rem);
		}
	}
	&.category_slider__button--next_mod {
		right: 0;
		transform: translate(-50%, -50%);

		@include bp($point_2, min) {
			margin-right: calc(50% - 50vw - 4px);
		}
		@include bp($point_3) {
			margin-right: calc(50% - 50vw - 1.2rem);
		}
	}
	&.swiper-button-disabled {
		opacity: 0;
		pointer-events: none;
	}

	@include bp($point_2, min) {
		margin-top: -0.8rem;
	}
	@include bp($point_3) {
		margin-top: -1.8rem;
	}
}

.category_slider__wrap {
	&.swiper {
		overflow: visible;

		@include bp($point_2, min) {
			margin-right: -2.5rem;
		}
		@include bp($point_3) {
			margin-right: -1.5rem;
		}
	}
}

.category_slider__item {
	min-height: clamp(28rem, 30vw, 48rem);

	&.swiper-slide {
		height: auto;
	}

	@include flex_block(column, nowrap, flex-start);

	@include bp($point_2, min) {
		padding-right: 2.5rem;
	}
	@include bp($point_3) {
		padding-right: 1.5rem;
	}
}
