@import "ScssHelpers/helpers-all.scss";

.banner_slider {
	padding: 0;
	background-color: $beige_1;
}

.banner_slider__body {
	position: relative;
}

.banner_slider__row {
	@include flex_block(row, wrap, flex-start, stretch);
}

.banner_slider__column {
	@include bp($point_2, min) {
		min-height: 68rem;

		&:nth-child(2n + 1) {
			width: 62.8%;
		}
		&:nth-child(2n + 2) {
			width: 37.2%;
		}
	}
	@include bp($point_3) {
		width: 100%;
	}
}

.banner_slider_img {
	position: relative;

	display: block;
	width: 100%;

	@include bp($point_2, min) {
		height: 100%;
	}

	@include bp($point_3) {
		padding-bottom: 85%;
	}
}

.banner_slider_img__in {
	@include stretch;
	@include cover_img;
}

.banner_slider__content {
	height: 100%;

	color: $black;

	@include bp($point_2, min) {
		padding: 9.4rem var(--gl-indent) 17rem 8rem;
	}
	@include bp($point_3) {
		padding: 3.3rem 2.9rem 9.6rem;
	}
}

.banner_slider__container {
	height: 100%;

	@include bp($point_2, min) {
		max-width: 40rem;
	}
}

.banner_slider__title {
	@include h2;

	line-height: 1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.5rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.4rem;
		}
	}

	@include bp($point_2, min) {
		letter-spacing: -.018em;
	}
	@include bp($point_3) {
		letter-spacing: -.024em;

		br {
			display: none;
		}
	}
}

.banner_slider__link {
	@include bp($point_6 - 1) {
		width: 100%;
	}
}

.banner_slider__text {
	@include text;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.6rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.8rem;
		}
	}

	@include bp($point_2, min) {
		line-height: 1.5;
		max-width: 41.1rem;
	}
	@include bp($point_3) {
		line-height: 1.43;
		letter-spacing: -.014em;
	}
}

.banner_slider__wrap {
	position: absolute;
	bottom: 0;
	right: 0;
	z-index: 3;

	@include bp($point_2, min) {
		width: 37.2%;
		margin-left: auto;
		padding: 0 2rem;
	}
	@include bp($point_3) {
		width: 100%;
		padding: 0 2.9rem 1.2rem;
	}
}

.banner_slider__controls {

	@include bp($point_2, min) {
		padding-left: 6rem;
		margin-bottom: 9rem;
	}
}

.banner_slider__button {
	flex-shrink: 0;

	&.banner_slider__button--prev_mod {
		&:not(:last-child) {
			margin-right: 0.7rem;
		}
	}
	&.banner_slider__button--next_mod {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-right: 2.3rem;
			}
			@include bp($point_3) {
				margin-right: 1.5rem;
			}
		}
	}
}
