@import "ScssHelpers/helpers-all.scss";

.advantages {
	@include bp($point_2, min) {
		padding-top: 3.2rem;
		padding-bottom: 3.4rem;
	}

	@include bp($point_3) {
		padding: 2.6rem 0 2.6rem 3rem;
	}
}

.advantages__body {
}

.advantages__list {
	// @include flex_block(row, nowrap, flex-start, center);
}

.advantages_item {
	// flex-shrink: 0;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 1.8rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
}

.advantages_item__wrap {
	display: flex;
	align-items: center;
}

.advantages_item__icon {
	flex-shrink: 0;

	border-radius: 50%;
	border: 2px solid $gold_2;

	&:not(:last-child) {
		margin-right: 1.5rem;
	}

	@include bp($point_4, min) {
		width: 5.2rem;
		height: 5.3rem;
	}

	@include bp($point_4 - 1) {
		width: 4rem;
		height: 4rem;
	}
}

.advantages_item__title {
	font-weight: 400;
	letter-spacing: -0.018em;

	@include bp($point_4, min) {
		font-size: 1.8rem;
		line-height: 1.33;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		line-height: 1.14;
	}
}
