@import "ScssHelpers/helpers-all.scss";

.category_item {
	position: relative;
	z-index: 1;

	display: block;
	flex: 1 1 auto;
	width: 100%;
	height: 100%;

	overflow: hidden;

	&::before,&::after{
		z-index: 1;

		pointer-events: none;
		content:'';

		@include stretch;
		@include transition_opacity;
	}
	&::before {
		background: linear-gradient(29deg, $black 0%, rgba($black, 0) 100%);
	}
	&::after {
		background: $gradient_2;
		opacity: 0;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		.category_item_img {
			scale: 1.06;
		}

		&::before {
			opacity: 0;
		}
		&::after {
			opacity: 0.8;
		}
	}

	@include flex_block(column, nowrap, flex-end);

	@include bp($point_2, min) {
		padding: 4.2rem 3.6rem;
	}
	@include bp($point_3) {
		padding: 2.8rem 2.4rem;
	}
}

.category_item_img {
	z-index: 0;

	transition: scale 0.3s ease;

	@include stretch;
}

.category_item_img__in {
	@include cover_img;
}

.category_item__content {
	position: relative;
	z-index: 2;

	margin-top: auto;

	color: $white;
}

.category_item__title {
	@include h3;

	line-height: 1.05;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.category_item__text {
	@include text;

	@include bp($point_2, min) {
		line-height: 1.5;
	}
	@include bp($point_3) {
		line-height: 1.14;
		letter-spacing: -.016em;
	}
}
