@import "ScssHelpers/helpers-all.scss";

.featured_products {
}

.featured_products__list {
	@include flex_block(row, wrap, flex-start, stretch);

	@include bp($point_2, min) {
		margin: 0 -2.4rem -5.1rem 0;
	}
	@include bp($point_3) {
		margin: 0 -1.6rem -2.5rem 0;
	}
}

.featured_products__item {
	@include bp($point_2, min) {
		width: 25%;
		padding: 0 2.4rem 5.1rem 0;
	}
	@include bp($point_3) {
		padding: 0 1.6rem 2.5rem 0;
	}
	@include bp($point_3, $point_4) {
		width: 33.333%;
	}
	@include bp($point_4 - 1) {
		width: 50%;
	}
}
